.category {
    display: flex;
    width: 100%;
}

.levelOneCategory {
    width: 28%;
}

.levelTwoCategory {
    width: 72%;
}

.categoryLevelOneItem {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 15px;
    color: gray;
}

.levelTwoCategoryName {
    padding: 8px 10px;
    color: rgb(70, 70, 70);
    font-size: 14px;
    font-weight: bold;
}
.levelThreeCategory {
    width: 100%;
    padding: 0px 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.levelThreeCategoryItem {
    width: 48%;
    padding-top: 8px;
}

.levelThreeCategoryImg {
    width: 100%;
    height: 40vw;
    object-fit: cover;
}

