.goodsMain {
    box-sizing: border-box;
    width: 100%;
    padding: 0 15px;
    margin-bottom: 47px;
}

.goodsTitle {
    color: rgba(80, 80, 80, 1);
    font-size: 16px;
    margin-top: 15px;
}

.goodsPriceDiv {
    display: flex;
    flex-direction: row;
    margin-top: 4px;
    align-items: flex-end;
}

.goodsPriceDiv .goodsPrice {
    color: red;
    font-size: 20px;
    font-weight: bold;
}

.goodsPriceDiv .goodsPriceOrg {
    color: grey;
    font-size: 15px;
    text-decoration: line-through;
    margin-left: 10px;
}

.goodsColorsDiv {
    margin-top: 15px;
}

.goodsColorsDiv .goodsColorsLabel {
    text-align: start;
    color: rgba(100, 100, 100, 1);
    font-size: 16px;
}

.goodsColorsDiv .goodsColors {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 8px;
    align-items: center;
}

.goodsDescriptionDiv {
    margin-top: 15px;
}

.goodsDescriptionDiv .goodsDescriptionLabel {
    color: rgba(100, 100, 100, 1);
    font-size: 16px;
}

.goodsDescriptionDiv .goodsDescription {
    margin-top: 8px;
    width: 100%;
}

.bottomButtonDiv {
    width: 100%;
    position: fixed;
    bottom: 0px;
    display: flex;
    flex-direction: row;
}

.bottomButton {
    height: 47px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-weight: 500;
    color: white;
    flex: 1;
}

.skuModalDiv {
    padding: 0 15px;
    width: 100%;
    box-sizing: border-box;
}

.skuModalDiv .skuModalHead {
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
}

.goodsSizeDiv {
    margin-top: 15px;
}

.goodsSizeDiv .goodsSizeLabel {
    text-align: start;
    color: rgba(100, 100, 100, 1);
    font-size: 16px;
}

.goodsSizeDiv .goodsSize {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
}

.goodsSizeDiv .goodsSizeTag {
    margin-right: 8px;
    margin-top: 8px;
}

.skuModalBottomButton {
    width: 100%;
    margin-top: 40px;
    height: 47px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-weight: 500;
    color: floralwhite;
    flex: 1;
    background-color: red;
}

